import React, { Component } from 'react'
import './index.scss'
import UploadImg from '../../../../components/UploadImg'

export class App extends Component {
  constructor(props) {
    super(props)
    this.state = {
      userState: '',
      token: '',
    }
  }

  getCardBtn = (pageId) => {
    const { loginState } = this.props
    if (loginState.returnId === 0) {
      return <a className="get-memberCard-btn" href={`/merchant/login/${pageId}?token=${loginState.token}`}>立即加入</a>
    } else if (loginState.returnId === 2) {
      return <a className="get-memberCard-btn" href={`/merchant/member-center/${pageId}?pageState=2`}>查看我的会员卡</a>
    } else {
      return <a className="get-memberCard-btn" href={`/merchant/member-center/${pageId}?pageState=1`} >领取会员卡</a>
    }
  }

  render() {
    const { items, value, templateData, pageId } = this.props
    const selectedItem = items.find(item => item.value === value) || value
    return <div className="card-s2">
      <h5 className="title">本店会员</h5>
      <div className="card-img-box">
        <img className="card-img" src={selectedItem.compImage ? selectedItem.compImage : value} alt="" />
        { templateData && templateData.storePicsrc
          ? <dl className="memberCard-info">
            <dt className="storeLogo-img">
              <img src={`http://timg.ffan.com/convert/resize/url_${templateData.storePicsrc}/tfs/1.png`} />
            </dt>
            <dd className="store-name">{templateData.storeName}</dd>
            <dd className="store-address">{templateData.storeAddress}</dd>
          </dl>
          : ''
        }
        <div className="img-shadow" />
      </div>
      {this.getCardBtn(pageId)}
    </div>
  }
}

export class Preview extends Component {
  render() {
    const { items, value, templateData } = this.props
    const selectedItem = items.find(item => item.value === value) || value
    return <div className="card-s2">
      <h5 className="title">本店会员</h5>
      <div className="card-img-box">
        <img className="card-img" src={selectedItem.compImage ? selectedItem.compImage : value} width={214} alt="" />
        { templateData && templateData.storePicsrc
          ? <img
            className="card-logo"
            src={`http://timg.ffan.com/convert/resize/url_${templateData.storePicsrc}/tfs/1.png`}
          /> : ''
        }
        <div className="img-shadow" />
      </div>
    </div>
  }
}

export class Preference extends Component {
  onChange = (_value) => {
    const { onChange, ...ele } = this.props
    onChange({ ...ele, value: _value.value })
  }

  render() {
    const { value, items, name, styleDisplay, size } = this.props
    return <div className="ele">
      <h2 className="detail-title" >{name}</h2>
      <div className="ele-content" >
        {items.map(item => <div className="item" style={styleDisplay} onClick={
        e => this.onChange({ id: item.id, value: item.value, })} >
          <img className="item-img" style={size} src={item.previewImage} alt="" />
          <div className="row-radio" >
            <input type="radio" checked={item.value === value}
            onChange={e => this.onChange({
              id: item.id, value: item.value
            })} className="radio-input" />
            <span className="radio-btn">
              <i className="iconfont icon-radio1" />
              <i className="iconfont icon-radio2" />
            </span>
            {item.name}
          </div>
        </div>)}
      </div>
      <div className="item-img-box">
        <div className="item-img" >
          <UploadImg imageUrl={value.image}
                     onChange={image => this.onChange({ value: image })}
                     resetUrl={value.initImg}
                     reset={false}
          />
        </div>
      </div>
    </div>
  }
}

export default {
  App,
  Preview,
  Preference,
}



