import React, { Component } from 'react'
import { getCookie } from 'utils/tools'
import './index.scss'

function isLogin () {
  return getCookie('boc_loginToken')
}

class List extends Component {
  buy(goods) {
    const { pageID, loginState } = this.props
    if (isLogin()) {
      window.location.href = `/merchant/order/${pageID}?immediately=true&goodsCode=${goods.goodsCode}`
    } else {
      window.location.href = `/merchant/login/${pageID}?token=${loginState.token}&true&` +
      `redirect=/merchant/home/${pageID}`
    }
  }
  render() {
    const { list, canPay } = this.props
    //todo 本期需求没有库存，此注释可保留至增加库存功能
    // return <ul>
    //   { list.map((item) => {
    //     return <li>
    //       <img className="commodity-pic" src={item.picList[0].pic} />
    //       <p className="commodity-name">{item.name}</p>
    //       { item.count !== 0
    //       ? <div>
    //         <p className="commodity-price">￥{item.priceCost}</p>
    //         {item.count <= 5 ? <p className="count">库存{item.count}</p> : null}
    //         <button onClick={() => this.buy(item.commodityId)}>购买</button>
    //       </div> : <p>已售完</p>
    //       }
    //     </li>
    //   })}
    // </ul>
    return <ul>
      { list.map((item) => {
        return <li className="item">
          { item.picList[0].pic
            ? <img
              className="commodity-pic"
              src={`http://timg.ffan.com/convert/resize/url_${item.picList[0].pic}/tfs/1.png`}
            /> : null
          }
          <div className="commodity-store-info">
            <p className="commodity-name">{item.name}</p>
            <div className="commodity-buy-box">
              <p className="commodity-price"><span>￥</span>{item.goodsSkuVOS[0].formatPriceCost}</p>
              { canPay !== 4001
                ? <button className="commodity-buy" onClick={() => this.buy(item)}>购买</button>
                : null
              }
            </div>
          </div>
        </li>
      })}
    </ul>
  }
}

export default List



