import { Upload, Icon, message } from 'antd'
import React, { Component } from 'react'
import PropTypes from 'prop-types'
/* eslint no-unused-vars: "off"  */
import fetchUtil from 'utils/fetchUtil'
import { getDomain } from 'utils/domain'
import './index.scss'

// function getBase64(img, callback) {
//   const reader = new FileReader()
//   reader.addEventListener('load', () => callback(reader.result))
//   reader.readAsDataURL(img)
// }

class Avatar extends Component {
  constructor() {
    super()
    this.state = {
      style: '',
      adjustStyle : this.adjustStyle.bind(this)
    }
  }

  beforeUpload = (file) => {
    this.file = file
    const isJPG = file.type === 'image/jpeg'
    const isPNG = file.type === 'image/png'
    if (!isJPG && !isPNG) {
      message.error('只允许上传PNG或JPG格式的图片!')
    }
    const isLt2M = file.size / 1024 / 1024 < 2
    if (!isLt2M) {
      message.error('Image must smaller than 2MB!')
    }

    return isJPG && isLt2M
  }

  customRequest = (option) => {
    const isJPG = option.file.type === 'image/jpeg'
    const isPNG = option.file.type === 'image/png'
    if (!isJPG && !isPNG) {
      return false
    }
    const that = this
    const uploadUrl = getDomain('/bo/v1/fileUpload', { suffix: 'com' })

    const formData = new FormData()
    formData.append('clientType', 1)
    formData.append('fileName', this.file)

    const onSuccess = res => {
      const img = res[ 'name_tfs' ][ 0 ]
      this.setState({ imageUrl: `http://img1.ffan.com/${img}` },
        () => this.props.onChange(that.state.imageUrl))
      this.changeImgFix(this.props.imageUrl)
    }

    const onError = e => {
      console.log(e)
      this.setState({ imageUrl: '' }, () => {
        this.props.onChange('')
      })
    }

    fetchUtil.postJSON(uploadUrl, formData, {
      formDataType: 'file',
      loginInfo: false,
    }).then(onSuccess, onError).catch(onError)
    return {
      onSuccess,
      onError,
      abort: () => {}
    }
  }

  componentDidMount() {
    const { imageUrl } = this.props
    if (imageUrl) {
      this.setState({ imageUrl })
    }
    this.changeImgFix(imageUrl)
  }

  handleChange = () => {
    const { onChange } = this.props
    const resetUrl = this.props.resetUrl
    if (!resetUrl) {
      return
    }

    onChange(resetUrl)
    this.changeImgFix(resetUrl)
  }

  adjustStyle = (wh) => {
    this.setState({
      style: wh
    })
  }

  changeImgFix(imgUrl) {
    const { adjustStyle } = this.state

    let img = new Image()
    img.onload = function() {
      const width = this.width
      const height = this.height
      if (width > height) {
        adjustStyle('w')
      } else {
        adjustStyle('h')
      }
    }
    img.setAttribute('src', imgUrl)
  }

  render() {
    const { disabled } = this.props
    const imageUrl = this.props.imageUrl

    return (
      <div>
        <Upload
          disabled={disabled}
          className={`avatar-uploader avatar-uploader-${this.state.style}`}
          name="avatar"
          accept="image/*"
          multiple={false}
          customRequest={this.customRequest}
          showUploadList={false}
          beforeUpload={file => {
            this.beforeUpload(file)
          }}
          onChange={this.handleChange} >{
          imageUrl ? <div className="img-container" >
            <img src={imageUrl} alt="" className="avatar" />
          </div> : <div className="img-container" ><Icon type="plus" className="avatar-uploader-trigger" />
            <span className="upload-text" >选择图片</span>
          </div> }
        </Upload>
        {this.props.reset === false
          ? ''
          : <span
            className="item-img-revoke"
            onClick={this.handleChange.bind(this)}
          ><i className="icon-revoke" />撤销</span>
        }
      </div>
    )
  }
}

Avatar.propTypes = {
  disabled: PropTypes.bool.isRequired,
  onChange: PropTypes.func.isRequired,
}

export default Avatar



