import React, { Component } from 'react'
import { getDomain } from 'utils/domain'
import fetchUtil from 'utils/fetchUtil'
import List from './List'

class CommodityList extends Component {
  constructor(props) {
    super(props)
    this.state = {
      list: [],
      url: '',
      pageCount: 0,
      loading: false,
      end: false,
      canPay: ''
    }
  }

  componentDidMount() {
    const url = getDomain('/bocxapi/v1/goods/goods_so', { suffix: 'com' })
    fetchUtil.getJSON(url, {
      pageId: this.props.pageID,
      pageSize: 30
    }).then(data => {
      this.setState({
        list : data.goodsListResult.goodsVOS,
        canPay: data.code
      })
    })
    if (this.props.loginState.returnId === 0) {
      this.setState({
        url: `/merchant/login/${this.props.pageID}?token=${this.props.loginState.token}&true&` +
        `redirect=/merchant/home/${this.props.pageID}`
      })
    } else {
      this.setState({
        url: `/merchant/order-list/${this.props.pageID}`
      })
    }

    //todo 分页待定
    // window.addEventListener('scroll', (e) => {
    //   const body = document.body
    //   if(this.state.end) {
    //     return false
    //   }
    //   if (body.scrollTop + body.offsetHeight === body.scrollHeight) {
    //     this.setState({
    //       loading: true
    //     })
    //     fetchUtil.getJSON(url, {
    //       pageId: this.props.pageID,
    //       pageNo: ++this.state.pageCount
    //     }).then(data => {
    //       if (!data) {
    //         this.setState({
    //           loading: false,
    //           end: true
    //         })
    //         return false
    //       }
    //       this.setState({
    //         list : this.state.list.concat(data.goodsVOS),
    //         loading: false
    //       })
    //     })
    //   }
    // })
  }

  render() {
    const { pageID, loginState } = this.props
    return <div className="commodity-wrap">
      <div className="commodity-list">
        <List {...this.state} pageID={pageID} loginState={loginState} />
      </div>
      <a className="my-order"
         href={this.state.url}>
        我的订单
        <i className="icon-into" />
      </a>
      {this.state.loading
        ? <img
          src="http://nres.ffan.com/newh5/2017527/4a063520edd71e81fe7e2b4f57a6b00606df4bac.svg"
          className="loading"
          width="32"
          height="32"
        /> : null
      }
    </div>
  }
}

export default CommodityList



