import React, { Component } from 'react'
import ReactQuill from 'react-quill'
import 'react-quill/dist/quill.snow.css'
import './index.scss'

export class App extends Component {
  render() {
    const { value } = this.props
    const html = { __html: value }
    return <div className="text-area-s2-preview">
      <div dangerouslySetInnerHTML={html} />
    </div>
  }
}

export class Preview extends Component {
  render() {
    const { value } = this.props
    const html = { __html: value }
    return <div className="text-area-s2">
      <div dangerouslySetInnerHTML={html} />
    </div>
  }
}

export class Preference extends Component {
  modules = {
    toolbar: [
      [{ 'header': [1, 2, false] }],
      ['bold', 'italic'],
      [{ 'size': ['small', false, 'large', 'huge'] }],
      [{ 'color': [] }]
    ]
  }

  formats = [
    'header',
    'bold', 'italic',
    'size',
    'color',
  ]

  onChange = (_value) => {
    const { onChange, ...ele } = this.props
    onChange({ ...ele, value: _value })
    this.setState({ _value })
  }

  render() {
    const { value, name } = this.props
    return <div className="ele">
      <h2 className="detail-title" >{name}</h2>
      <ReactQuill theme="snow" modules={this.modules} formats={this.formats} value={value}
                  onChange={this.onChange} />
    </div>
  }
}

export default {
  App,
  Preview,
  Preference,
}



