import React, { Component } from 'react'
import Home from '../components/H5Home/H5Home'
import fetchUtil from 'utils/fetchUtil'
import { getDomain, fixDomain } from 'utils/domain'
import UserViewData from './data'
import { getCookie, getUrlParam } from 'utils/tools'

export const Promised = (Wrapped) => class extends Component {
  constructor(props) {
    super(props)
    this.state = {
      preview: {
        elements: [],
        layouts: [],
      },
      loginState: {}
    }
    this.getPageInfo = this.getPageInfo.bind(this)
    this.getLoginInfo = this.getLoginInfo.bind(this)
  }

  getPageInfo() {
    const url = getDomain('/app/v1/bo/v1/web/merchant/page/' + this.props.params.pageID)
    return fetchUtil.getJSON(url).then(data => {
      return data
    })
  }

  getLoginInfo() {
    if (getCookie('boc_loginToken')) {
      const url = fixDomain('http://api.sit.ffan.net/bocxapi/v1/oauth/storeUser')
      return fetchUtil.getJSON(url, {
        loginToken: getCookie('boc_loginToken'),
        storeId: getCookie('boc_storeId'),
      })
    } else {
      if (sessionStorage.getItem('boc_login_data')) {
        return Promise.resolve(JSON.parse(sessionStorage.getItem('boc_login_data')))
      }
      const url = fixDomain('http://api.sit.ffan.net/bocxapi/v1/oauth/token')
      const pageId = this.props.params.pageID
      const options = getUrlParam('debug') ? {
        mobile: getUrlParam('mobile'),
        debug: 1,
        pageId: pageId
      } : {
        code: getUrlParam('code'),
        pageId: pageId
      }
      return fetchUtil.postJSON(url, options)
    }
  }

  async componentDidMount() {
    document.title = '详情'
    Promise.all([
      this.getPageInfo(),
      this.getLoginInfo(),
    ]).then(datas => {
      const pageData = datas[0]
      const loginData = datas[1]
      if (!sessionStorage.getItem('boc_login_data')) {
        sessionStorage.setItem('boc_login_data', `${JSON.stringify(loginData)}`)
      }
      if (typeof pageData.viewData === 'string') {
        pageData.viewData = JSON.parse(pageData.viewData)
      }
      this.setState({ preview: pageData.viewData, loginState: loginData })
    }).catch(err => {
      console.log('---UserView data error')
      console.log(err)
      this.setState({ preview: UserViewData })
    })
  }

  render() {
    //todo loginstate 频道preview中
    return <Wrapped {...this.state.preview} loginState={this.state.loginState} {...this.props} />
  }
}

export default Promised(Home)



