import React, { Component } from 'react'
import './index.scss'

export class App extends Component {
  render() {
    const { items, value } = this.props
    const selectedItem = items.find(item => item.value === value)
    return <img src={selectedItem.compImage} alt="" />
  }
}

export class Preview extends Component {
  render() {
    const { items, value } = this.props
    const selectedItem = items.find(item => item.value === value)
    return <img src={selectedItem.compImage} width={214} alt="" />
  }
}

export class Preference extends Component {
  onChange = (_value) => {
    const { onChange, ...ele } = this.props
    onChange({ ...ele, value: _value.value })
  }

  render() {
    const { value, items, name, styleDisplay, size } = this.props
    return <div className="ele">
      <h2 className="detail-title" >{name}</h2>
      <div className="ele-content" >
        {items.map(item => <div className="item" style={styleDisplay} onClick={
          e => this.onChange({ id: item.id, value: item.value, })} >
          <img className="item-img" style={size} src={item.previewImage} alt="" />
          <div className="row-radio" >
            <input type="radio" checked={item.value === value}
                   onChange={e => this.onChange({
                     id: item.id, value: item.value
                   })} className="radio-input" />
            <span className="radio-btn">
              <i className="iconfont icon-radio1" />
              <i className="iconfont icon-radio2" />
            </span>
            {item.name}
          </div>
        </div>)}
      </div>
    </div>
  }
}

export default {
  App,
  Preview,
  Preference,
}



