import React, { Component } from 'react'
import classNames from 'classnames'
import './index.scss'

export class App extends Component {
  render() {
    const { templateData } = this.props
    if (!templateData) {
      return false
    }
    return <div className={classNames('basic-info', `layout-${this.props.value}`)} >
      <img
        src={`http://timg.ffan.com/convert/resize/url_${templateData.bgPic}/tfs/1.png`}
        className="bg"
      />
      <div className="logo" >
        <img
          src={`http://timg.ffan.com/convert/resize/url_${templateData.storePicsrc}/tfs/1.png`}
          className="logo-img"
        />
        <h2 className="name" >{templateData.storeName}</h2>
      </div>
      <div className="contact" >
        <div className="address" >
          <img src="http://timg.ffan.com/convert/resize/url_T1t9CTB_C_1RCvBVdK/tfs/1.png" className="site" />
          <span className="site-text">{templateData.storeAddress}</span>
        </div>
        <a href="tel:'+{templateData.storePhone}+'" className="contact-phone" >
          <img src="http://timg.ffan.com/convert/resize/url_T1nndTBXWj1RCvBVdK/tfs/1.png" className="contact-tel" />
        </a>
      </div>
    </div>
  }
}

export class Preview extends Component {
  render() {
    const { templateData } = this.props
    if (!templateData) {
      return false
    }
    return <div className={classNames('basic-info', `layout-${this.props.value}`)} >
      <img
        src={`http://timg.ffan.com/convert/resize/url_${templateData.bgPic}/tfs/1.png`}
        className="bg"
      />
      <div className="logo" >
        <img
          src={`http://timg.ffan.com/convert/resize/url_${templateData.storePicsrc}/tfs/1.png`}
          className="logo-img"
        />
        <h2 className="name" >{templateData.storeName}</h2>
      </div>
      <div className="contact">
        <div className="address" >
          <img src="http://timg.ffan.com/convert/resize/url_T1t9CTB_C_1RCvBVdK/tfs/1.png" className="site" />
          <span className="site-text">{templateData.storeAddress}</span>
        </div>
        <a href={`tel:${templateData.storePhone}`} className="contact-phone" >
          <img src="http://timg.ffan.com/convert/resize/url_T1nndTBXWj1RCvBVdK/tfs/1.png" className="contact-tel" />
        </a>
      </div>
    </div>
  }
}

export class Preference extends Component {
  onChange = (_value) => {
    const { onChange, ...ele } = this.props
    onChange({ ...ele, value: _value.value })
  }

  render() {
    const { value, items, name } = this.props

    return <div className="ele">
      <h2 className="detail-title" >{name}</h2>
      <div className="ele-content" >
        {items.map(item => <div className="item" onClick={
          e => this.onChange({ id: item.id, value: item.value, })} >
          <img className="item-img" src={item.previewImage} alt="" />
          <div className="row-radio" >
            <input type="radio" checked={item.value === value}
                   onChange={e => this.onChange({
                     id: item.id, value: item.value
                   })} className="radio-input" />
            <span className="radio-btn">
              <i className="iconfont icon-radio1" />
              <i className="iconfont icon-radio2" />
            </span>
            {item.name}
          </div>
        </div>)}
      </div>
    </div>
  }
}

export default {
  App,
  Preview,
  Preference,
}



