import React, { Component } from 'react'
import Commodity from './CommodityList'
import './index.scss'

export class App extends Component {
  render() {
    return <div ref="dragdown">
      <Commodity pageID={this.props.pageId} loginState={this.props.loginState} />
    </div>
  }
}

export class Preview extends Component {
  render() {
    return <div className="card-s2" >
      <img
        className="card-img"
        src="http://timg.ffan.com/convert/resize/url_T1fqLTBb__1RCvBVdK/tfs/1.png"
        width={214}
        alt=""
      />
    </div>
  }
}

export class Preference extends Component {
  onChange = (_value) => {
    const { onChange, ...ele } = this.props
    onChange({ ...ele, value: _value.value })
  }

  render() {
    return <div className="ele" />
  }
}

export default {
  App,
  Preview,
  Preference,
}



