import Elements from '../index'
import React, { Component } from 'react'
import classNames from 'classnames'
import { getRandomString } from 'utils/randomString'
import './ListContainer.scss'

export class App extends Component {
  render() {
    const { childrenType, value, ...rest } = this.props
    const Element = Elements[ childrenType ]
    const Preview = Element[ 'App' ]
    if (!Element) {
      return <div>not found</div>
    }

    return <div className={classNames('ele-container-app',
      `list-container-${childrenType}`)} >
      { value.map(ele => <Preview {...ele} {...rest} />
      )}
    </div>
  }
}

export class Preview extends Component {
  render() {
    const { childrenType, value, ...rest } = this.props
    const Element = Elements[ childrenType ]
    const Preview = Element[ 'Preview' ]
    if (!Element) {
      return <div>not found</div>
    }

    if (!value.length) {
      return <div className="blank-template" >
        <img className="blank-template-img"
             src="http://timg.ffan.com/convert/resize/url_T18iETBXDv1RCvBVdK/tfs/1.png" />
        <p className="blank-template-info" >添加一个模板吧~</p>
      </div>
    }

    return <div className={classNames('ele-container',
      `list-container-${childrenType}`)} >
      { value.map(ele => <Preview {...ele} {...rest} />
      )}
    </div>
  }
}

export class Preference extends Component {
  onChange(_value) {
    const { value, onChange, ...element } = this.props
    const newValue = value.map(ele => (ele.id === _value.id
      ? { ...ele, ..._value }
      : ele))
    onChange({ ...element, value: newValue })
  }

  addElement = () => {
    const { childrenType, value, onChange, ...element } = this.props
    const Element = Elements[ childrenType ]
    onChange({
      ...element, childrenType, value: [ ...value, { id: getRandomString({}), ...Element.initValue() } ]
    })
  }

  removeElement = (id) => {
    const { value, onChange, ...element } = this.props
    onChange({
      ...element, value: value.filter(ele => ele.id !== id)
    })
  }

  render() {
    const { childrenType, value, ...rest } = this.props
    //console.log(JSON.stringify(value))
    const Element = Elements[ childrenType ]
    if (!Element) {
      return <div>not found</div>
    }
    const Preference = Element[ 'Preference' ]
    return <div className="preference-container" >
      <h2 className="detail-title" >{this.props.name}</h2>
      {value.map(ele => <div key={ele.id} className="grid-item" >
        <Preference value={ele} {...rest} onChange={value => this.onChange(value.value)} />
        <i className="del" onClick={this.removeElement.bind(this, ele.id)} />
      </div>)}
      <div className="item-action" >
        <button onClick={this.addElement} className="add" ><i className="iconfont icon-fileadd" />增加</button>
      </div>
    </div>
  }
}

export default {
  App,
  Preview,
  Preference,
}



