import React, { Component } from 'react'
import './index.scss'
import classNames from 'classnames'
import randomstring from 'randomstring'
import compile from 'es6-template-strings'
import UploadImg from '../../../../components/UploadImg'

const getStyle = (styles, data) => {
  if (!styles) return {}
  let res = {}
  Object.keys(styles).map(key => {
    res[ key ] = compile(styles[ key ], data)
  })
  return res
}

export class App extends Component {
  render() {
    const { styleType, link, styles, ...rest } = this.props
    return <a href={link} className={`ele-${styleType}-app`} >
      <div style={getStyle(styles, rest)} className="item" />
    </a>
  }
}

export class Preview extends Component {
  render() {
    const { styleType, styles, ...rest } = this.props
    return <div className={`ele-${styleType}-preview`} style={getStyle(styles, rest)} />
  }
}

export class Preference extends Component {
  onChange = (_value) => {
    const { onChange, ...ele } = this.props
    onChange({ ...ele, value: { ...ele.value, ..._value } })
  }

  render() {
    const { value, styleType } = this.props
    return <div className={classNames('itemWrap', `ele-${styleType}-preference`)} >
      <ul className="item" >
        <li className="item-con" >
          <span className="name" >图片</span>
          <div className="item-img-box">
            <div className="item-img" >
              <UploadImg imageUrl={value.image}
                         onChange={image => this.onChange({ image: image })}
                         resetUrl={value.initImg}
              />
            </div>
            <span className="img-rule">图片规则说明</span>
          </div>
        </li>
        <li className="item-con" >
          <span className="name" >链接</span>
          <input className="inputCommon" type="text" value={value.link}
                 onChange={e => this.onChange({ link: e.target.value })} />
        </li>
      </ul>
    </div>
  }
}

const randomLightColor = () => randomstring.generate({ charset: 'ABCD', length: 3 })
const randomDarkColor = () => randomstring.generate({ charset: '2356', length: 3 })
//const randomText = () => randomstring.generate({ charset: 'alphanumeric', length: 2, capitalization: 'uppercase' })

const randomLink = () => {
  const links = [
    'http://www.sina.com.cn/',
    'https://www.baidu.com/',
    'http://www.weibo.com/'
  ]
  return links[ parseInt(randomstring.generate({ charset: '012', length: 1 }), 10) ]
}

const initValue = () => ({
  link: randomLink(),
  image: `http://temp.im/73x50/${randomLightColor()}/${randomDarkColor()}`,
})

export default {
  App,
  Preview,
  Preference,
  initValue,
}



