import React, { Component } from 'react'
import Elements from 'routes/Mobile/routes/H5Editor/Elements'
import Container from 'routes/Mobile/routes/H5Editor/Elements/container'
import './H5Home.scss'

export class H5Home extends Component {
  renderElement(ele, loginState) {
    if (Container[ ele.type ]) {
      const { App } = Container[ ele.type ]
      return <App {...ele} pageId={this.props.params.pageID} loginState={loginState} />
    }

    if (Elements[ ele.type ]) {
      const { App } = Elements[ ele.type ]
      return <App {...ele} pageId={this.props.params.pageID} loginState={loginState} />
    }
  }

  renderPreview(elements, loginState) {
    return elements.map(ele => <div key={`preview-${ele.id}`} className="home-ele">
      {this.renderElement(ele, loginState)}
    </div>)
  }

  render() {
    const { elements, loginState } = this.props
    return <div className="h5-fs-home" >
      {this.renderPreview(elements, loginState)}
    </div>
  }
}

export default H5Home



