\n      left: 0;\n      display: flex;\n      flex-wrap: nowrap;\n      align-items: center;\n    }\n    .name{\n      padding-left: 86px;\n      width: 288px;\n      font-size: 16px;\n      color: #000;\n      @include text-overflow;\n    }\n  }\n  .layout-right{\n    .logo{\n      position: absolute;\n      top: 0;\n      left: 50%;\n      margin-left: -100px;\n      width: 200px;\n    }\n    .name{\n      width: 276px;\n      font-size: 16px;\n      color: #000;\n      text-align: center;\n      @include text-overflow;\n      display: block;\n    }\n  }\n}\n.ele {\n    overflow: hidden;\n    .detail-title {\n        @include detailTitle;\n    }\n    .item {\n        display: inline-block;\n        .item-img {\n            width: 83px;\n            height: 42px;\n            cursor: pointer;\n        }\n        .row-radio {\n            position: relative;\n            margin: 6px 0;\n            cursor: pointer;\n            font-size: 12px;\n            color: $radio-color;\n            .radio-btn {\n                margin-right: 2px;\n                vertical-align: -3px;\n            }\n        }\n        .radio-input {\n            position: absolute;\n            top: 2px;\n            left: -3px;\n            width: 16px;\n            height: 15px;\n            opacity: 0;\n            cursor: pointer;\n            z-index: 1;\n        }\n        @include rowRadio($radio-color);\n    }\n    .item:first-child {\n        margin-right: 14px;\n    }\n}\n\n@media screen and (min-height : 601px) {\n  .preview-ele {\n    .layout-left {\n        .logo {\n          padding: 35px 16px 0;\n          .logo-img {\n            width: $logo-img-width;\n            height: $logo-img-width;\n            vertical-align: middle;\n          }\n          .name {\n            padding-left: 6px;\n            width: $editor-c-preview-width - $logo-img-width - 40px;\n            font-size: 14px;\n            vertical-align: middle;\n          }\n        }\n    }\n    .layout-right{